<template>
    <div class="section no-pad-bot" id="index-banner">
        <div class="container" style="margin-bottom: 50px;">
            <div class="row">
                <div class="col offset-m3 m6">
                    <h1 class="text-center" style="margin-bottom: 50px; text-align: center;">NG NHP</h1>

                    <!-- Form ng nhp -->
                    <form method="POST" v-on:submit.prevent="doLogin">
                        <div class="form-group">
                            <label class="text-white">Nhp email ca bn:</label>
                            <input type="email" class="form-control" name="email" autocomplete="off" required />
                        </div>

                        <br />

                        <div class="form-group">
                            <label class="text-white">Nhp mt khu ca bn:</label>
                            <input type="password" class="form-control" name="password" autocomplete="off" required />
                        </div>

                        <br />

                        <!-- Nt submit ng nhp -->
                        <input type="submit" value="Vo ca hng" v-bind:disabled="isLoading" name="submit"
                            class="btn btn-primary" />
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from "axios";
import swal from "sweetalert2";
import store from "../vuex/store";
import "materialize-css/dist/css/materialize.min.css";
import "materialize-css/dist/js/materialize.min.js";

export default {
    data() {
        return {
            // Trng thi ang ti khi ngi dng thc hin ng nhp
            isLoading: false,
        };
    },

    methods: {
        // Hm thc hin ng nhp
        async doLogin(event) {
            const form = event.target; // Ly form t s kin
            const formData = new FormData(form); // To d liu form

            this.isLoading = true; // Kch hot trng thi ti

            try {
                // Gi yu cu ng nhp ti API
                const response = await axios.post(this.$apiURL + "/login", formData);

                // Hin th thng bo kt qu
                swal.fire("Login", response.data.message, response.data.status);

                if (response.data.status === "success") {
                    // Reset form nu ng nhp thnh cng
                    form.reset();

                    // Ly access token t phn hi ca server
                    const accessToken = response.data.accessToken;
                    // Lu token vo localStorage
                    localStorage.setItem(this.$accessTokenKey, accessToken);

                    // Cp nht headers mc nh vi token
                    this.$headers = {
                        "Content-Type": "application/json",
                        Authorization: "Bearer " + accessToken,
                    };

                    // Cp nht trng thi ngi dng trong Vuex store
                    store.commit("setLogin", true);
                    store.commit("setUser", response.data.user);

                    // Khi to dropdown sau khi ng nhp
                    this.initDropdown();

                    // Chuyn hng v trang ch
                    this.$router.push("/");
                }
            } catch (error) {
                // X l li ng nhp
                swal.fire("Error", "ng nhp tht bi. Vui lng th li.", "error");
            } finally {
                // Tt trng thi ti
                this.isLoading = false;
            }
        },

        // Hm khi to dropdown
        initDropdown() {
            // m bo DOM  sn sng trc khi khi to
            this.$nextTick(() => {
                const elems = document.querySelectorAll(".dropdown-trigger");
                if (elems.length > 0) {
                    // eslint-disable-next-line no-undef
                    M.Dropdown.init(elems, {});
                }
            });
        },
    },

    mounted() {
        // Khi to dropdown sau khi component  render
        this.initDropdown();
    },
};
</script>
<style>
/* nh dng nhn */
label {
    font-size: initial;
}
</style>
